import React from "react";
import NotFound from "./views/NotFound";
import DocValidateModule from "./views/demoValidator/demoValidator";
import { Switch, Route } from "react-router-dom";

class Main extends React.Component {
	render() {
		return (
			<Switch>
				<Route
					exact
					path="/"
					component={DocValidateModule}
				></Route>
				<Route
					exact
					path="/doc-validate"
					component={DocValidateModule}
				></Route>
				<Route
					path="*"
					component={NotFound}
				></Route>
			</Switch>
		);
	}
}

export default Main;
